@font-face {    font-family: "Inter Other";    font-style: normal;    font-weight: 700;    src: url("../fonts/Inter-Bold.woff2") format("woff2"),    url("../fonts/Inter-Bold.woff") format("woff"),    url("../fonts/Inter-Bold.ttf") format("ttf");    font-display: swap;}@font-face {    font-family: "Futura PT";    font-style: normal;    font-weight: 400;    src: url("../fonts/FuturaPT-regular.woff2") format("woff2"),    url("../fonts/FuturaPT-regular.woff") format("woff"),    url("../fonts/FuturaPT-regular.ttf") format("ttf");    font-display: swap;}@font-face {    font-family: "Futura PT";    font-style: normal;    font-weight: 500;    src: url("../fonts/FuturaPT-medium.woff2") format("woff2"),    url("../fonts/FuturaPT-medium.woff") format("woff"),    url("../fonts/FuturaPT-medium.ttf") format("ttf");    font-display: swap;}.visually-hidden {    position: absolute;    width: 1px;    height: 1px;    margin: -1px;    border: 0;    padding: 0;    white-space: nowrap;    clip-path: inset(100%);    clip: rect(0 0 0 0);    overflow: hidden;}*, *::before, *::after {    box-sizing: border-box;}body {    margin: 0;    font-family: 'Futura PT', sans-serif;    font-size: 18px;    line-height: 23px;    color: #000;}