.product {    width: 100%;    max-width: 1320px;    padding: 0 40px;    margin: 0 auto;    display: grid;    grid-template-columns: repeat(4, minmax(200px, 280px));    grid-template-rows: 99px minmax(89px, auto) minmax(124px, auto) auto auto;    column-gap: 40px;    justify-content: center;}/*--- Section Header ---*/.product__header {    grid-column: -1 / -3;    grid-row: 2;    max-width: 513px;    width: 100%;    justify-self: end;}.product__top {    display: flex;    justify-content: space-between;    align-items: center;}.product__title {    font-family: "Inter Other", Inter, sans-serif;    margin: 0;    font-weight: 700;    font-size: 25px;    line-height: 30px;    max-width: 470px;}.product__price {    margin-top: 5px;}/*--- Section Interactive ---*/.product__interactive {    grid-column: -1 / -3;    grid-row: 3;    justify-self: end;    max-width: 527px;    width: 100%;    margin-right: -14px;}.product__buttons {    display: grid;    grid-template-columns: 0.8fr 1fr;    margin-bottom: 33px;}.bin__buttons {    display: flex;    column-gap: 25px;    position: relative;}.product__sizeList {    display: grid;    grid-template-columns: repeat(2, 50%);    grid-auto-flow: column;    align-items: center;}.product__link {    text-decoration: none;    color: inherit;    font-size: 16px;    line-height: 21px;}.product__link:hover {    text-decoration: underline;}.product__description {    padding: 44px 60px 0 0;    grid-column: -1 / -3;    grid-row: 4;    font-size: 18px;    line-height: 23px;    max-width: 513px;    width: 100%;    justify-self: end;}.product__description p {    margin: 0 0 22px;}/*--- Media ---*/@media (max-width: 1140px) {    .product {        padding-top: 36px;        grid-template-columns: repeat(6, minmax(98px, 140px));        grid-template-rows: minmax(89px, auto) minmax(124px, auto) auto 1fr;        column-gap: 20px;    }    .product__header {        grid-column: -1 / -4;        grid-row: 1;    }    .product__interactive {        grid-column: -1 / -4;        grid-row: 2;        justify-self: start;        grid-template-columns: 1fr;        gap: 0;    }    .product__description {        grid-column: -1 / -4;        grid-row: 3;        padding-right: 0;    }}@media (max-width: 830px) {    .product__buttons {        grid-template-columns: 1fr;        margin-bottom: 33px;        row-gap: 6px;    }    .product__description {        padding-top: 36px;    }}@media (max-width: 718px) {    .product {        padding: 26px 20px 40px;        display: flex;        align-items: center;        flex-direction: column;    }    .product__header {        margin-bottom: 17px;    }    .product__title {        font-size: 20px;        line-height: 24px;    }    .product__price {        line-height: 21px;        font-size: 16px;    }    .product__interactive {        display: flex;        flex-direction: column;        margin: 0;    }    .product__buttons {        order: 1;    }    .product__sizeList {        margin-bottom: 29px;    }    .product__link {        justify-self: end;    }    .product__description {        padding-top: 23px;    }    .product__sizeList {        grid-template-columns: minmax(auto, 270px) auto;    }}