.list {    padding: 0;    list-style: none;    margin: 0;    grid-column: 1;    grid-row: 3 / -1;    justify-self: start;    align-self: start;}.item {    margin: 0;}.img {    display: block;    max-height: 110px;    height: 100%;    width: auto;}@media (max-width: 1140px) {    .list {        grid-column: -1 / -4;        grid-row: 4;        display: flex;        flex-wrap: wrap;        margin-top: 10px;    }    .img {        max-height: 67px;    }}@media (max-width: 718px) {    .list {        align-self: start;    }}